'use strict';

var _typeof = typeof Symbol === "function" && typeof Symbol.iterator === "symbol" ? function (obj) { return typeof obj; } : function (obj) { return obj && typeof Symbol === "function" && obj.constructor === Symbol && obj !== Symbol.prototype ? "symbol" : typeof obj; };

layui.define([], function (exports) {
    exports("actionTpl", {
        id: function id(key) {
            return '<div><span class="jl-control-' + key + '"">{{ d.' + key + ' }}</span></div>';
        },
        input: function input(key, option, isFirstLetter, readOnly) {
            var strs = '';
            var key_text = key + "_text";
            if (option) {
                strs = '<input type="text" value="' + (option[key_text] ? option[key_text] : option[key]) + '" name="' + key + 'title"  class="layui-input jl-control-' + key + ' ' + (isFirstLetter ? 'layui-form-label-pinyin-customer' : '') + '" >' + '<input type="hidden" value="' + option[key] + '" name="' + key + '" autocomplete="off" class="layui-input jl-control-' + key + '-id">';
            } else {
                strs = '<input type="text" value=" {{d.' + key + '}} " name="title" autocomplete="off" class="layui-input ' + key + '  ' + (isFirstLetter ? 'layui-form-label-pinyin-customer' : '') + ' jl-control-' + key + '">';
            }
            if (readOnly) {
                strs = '<input type="text" value="' + (option[key_text] ? option[key_text] : option[key]) + '" name="' + key + 'title"  class="layui-input jl-control-' + key + ' ' + (isFirstLetter ? 'layui-form-label-pinyin-customer' : '') + '" readonly >';
            }
            return '<div>' + strs + '</div>';
        },
        checkInput: function checkInput(key, option, readOnly) {
            var strs = '';
            var key_text = key + "_text";
            if (option) {
                strs = '<input type="text" value="' + (option[key_text] ? option[key_text] : option[key]) + '" name="' + key + 'title"  class="layui-input jl-control-' + key + '" >' + '<input type="hidden" value="" name="' + key + '_check" autocomplete="off" class="layui-input jl-control-' + key + '-check">';
            }
            if (readOnly) {
                strs = '<input type="text" value="' + (option[key_text] ? option[key_text] : option[key]) + '" name="' + key + 'title"  class="layui-input jl-control-' + key + '" readonly >';
            }
            return '<div>' + strs + '</div>';
        },
        inputNum: function inputNum(key) {
            return '<div>' + '<input type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input jl-control-' + key + '">' + '</div>';
        },
        inputInput: function inputInput(key, d) {
            var keyStart = key + "_start";
            var keyEnd = key + "_end";
            return '<div>' + '<input type="number" value="' + d[keyStart] + '" name="' + keyStart + '"  style="width:40%;display:inline-block;text-align:center" min="0" max="99999999"  autocomplete="off" class="jl-limit-min layui-input jl-control-' + key + '_start"> — ' + '<input type="number"  value="' + d[keyEnd] + '" name="' + keyEnd + '"  style="width:40%;display:inline-block;text-align:center" autocomplete="off" min="0" max="99999999" class="jl-limit-max layui-input jl-control-' + key + '_end">' + '</div>';
        },
        input_one: function input_one(key, d) {
            return '<div>' + '<input type="text" value="' + d[key] + '" name="' + key + '" style="width:100%;display:inline-block;text-align:center" autocomplete="off" class="layui-input jl-control-' + key + '"> ' + '</div>';
        },
        inputReadOnly: function inputReadOnly(key, option, readonly) {
            var strs = '';
            var key_text = key + "_text";
            //console.log(option);
            if (option) {
                strs = '<input type="text" readonly value="' + (option[key_text] ? option[key_text] : option[key] ? option[key] : "") + '" name="title" autocomplete="off" class="layui-input jl-control-' + key_text + '">' + '<input type="hidden" readonly value="' + option[key] + '" name="' + key + '" autocomplete="off" class="layui-input jl-control-' + key + '">';
            } else {
                strs = '<input type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input jl-control-' + key + '">';
            }
            if (readonly) {
                strs = '<input type="text" readonly value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input jl-control-' + key + '">';
            }
            return '<div>' + strs + '</div>';
        },
        time: function time(key) {
            return '<div><input type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input time-tpl-input jl-control-' + key + '"></div>';
        },
        checkbox: function checkbox(key) {
            return '<div class="layui-input-block"> ' + '{{#  if(d.' + key + '==1){ }}' + '<input type="checkbox" name="" checked lay-filter="checkbox" lay-skin="primary" class="jl-control-' + key + '">' + '{{#  } else { }}' + '<input type="checkbox" name="" lay-filter="checkbox" lay-skin="primary" class="jl-control-' + key + '">' + '{{#  } }}' + '</div>';
        },
        select: function select(key, option) {
            var optionHtml = '';
            $.each(option, function (index, value) {
                optionHtml += '{{#  if(d.' + key + '==="' + value.value + '"){ }}' + '<option value="' + value.value + '" selected>' + value.title + '</option>' + '{{#  } else { }}' + '<option value="' + value.value + '">' + value.title + '</option>' + '{{#  } }}';
            });
            return '<div>' + '{{#  if(d._' + key + '){ }}' + //_disabled
            '<div class="disabled-container disabled-active">' + '<div class="disabled-mark"></div>' + '<select class="jl-control-' + key + '">' + optionHtml + '</select>' + '</div>' + '{{#  } else { }}' + '<div class="disabled-container">' + '<div class="disabled-mark"></div>' + '<select class="jl-control-' + key + '">' + optionHtml + '</select>' + '</div>' + '{{#  } }}' + '</div>';
        },
        select_type: function select_type(key, option, d) {
            var optionHtml = '';
            $.each(option, function (index, value) {
                if (value.group) {
                    var temporary_option = '';
                    $.each(value.option, function (ind, val) {
                        if (d[key] == val.value) {
                            temporary_option += '<option value="' + val.value + '" selected>' + val.title + '</option>';
                        } else {
                            temporary_option += '<option value="' + val.value + '">' + val.title + '</option>';
                        }
                    });
                    optionHtml += '<optgroup label="' + value.title + '">' + temporary_option + '</optgroup>';
                } else {
                    if (d[key] === 10000 || !d[key]) {
                        optionHtml += '<option value="' + value.value + '" selected>' + value.title + '</option>';
                    } else {
                        optionHtml += '<option value="' + value.value + '">' + value.title + '</option>';
                    }
                }
            });
            return '<div class="layui-form">' + '<div class="layui-form-item">\n' + '    <div class="layui-inline">\n' + '      <div class="layui-input-inline" style="width:116px;text-align:left">\n' + '        <select name="quiz" class="jl-control-' + key + '">\n' + optionHtml + '        </select>\n' + '      </div>\n' + '    </div>' + '</div>';
        },
        selectBargain: function selectBargain(key, option) {
            var optionHtml = '';
            if (option[key]) {
                if (option[key].length > 0) {
                    $.each(option[key], function (index, value) {
                        if (index == 0) {
                            optionHtml += '<option value="' + (option.fitemno || value.fitemno) + '" selected>' + (option.fitemno || value.fitemno) + '</option>';
                        } else {
                            optionHtml += '<option value="' + value.fitemno + '" >' + value.fitemno + '</option>';
                        }
                    });
                }
            }

            return '<div class="select-brand-container"><span class="jl-control-' + key + '-span" style="display:none">' + JSON.stringify(option) + '</span>' + '<select style="width: 100px" class="jl-control-' + key + '" lay-verify="" lay-search>' + optionHtml + '</select>' + '</div>';
        },
        selectBrand: function selectBrand(key, option) {
            var optionHtml = '';
            $.each(option, function (index, value) {
                optionHtml += '{{#  if(d.' + key + '==="' + value.id + '"){ }}' + '<option value="' + value.id + '" selected>' + value.brand_name + '</option>' + '{{#  } else { }}' + '<option value="' + value.id + '">' + value.brand_name + '</option>' + '{{#  } }}';
            });
            return '<div class="select-brand-container">' + '<select style="width: 100px" class="jl-control-' + key + '" lay-verify="" lay-search>' + optionHtml + '</select>' + '</div>';
        },
        input_delivery: function input_delivery(key) {
            return '<div>' + '<input style="display: inline-block;width: 78px;margin-right: 10px" type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input jl-control-' + key + '">天' + '</div>';
        },
        input_earnest: function input_earnest(key) {
            return '<div>' + '<input style="display: inline-block;width: 70px;margin-right: 10px" type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="layui-input jl-control-' + key + '">%' + '</div>';
        },
        selectTap: function selectTap(key, d) {
            return '<div>' + '<button data-option="' + d[key] + ' " class="select-tap layui-btn layui-btn-sm jl-control-' + key + '" style="background-color:' + (d[key] ? "#009688" : "#c2c2c2") + '">查看详情</button>' + '</div>';
        },
        select_erp_Tap: function select_erp_Tap(key, d) {
            return '<div>' + '<span style="display: none">  ' + JSON.stringify(d[key] || []) + '</span>' + '<button data-option class="select-erp-tap layui-btn layui-btn-sm jl-control-' + key + '"  style="background-color:' + (d[key] ? "#009688" : "#c2c2c2") + '">查看详情</button>' + '</div>';
        },
        selectCategory: function selectCategory(key, option) {
            var str = option.isMore ? '{{ d.cateList?(d.cateList[0]?d.cateList[0].cate_name:"无"):"无"||"无" }}' : '{{ d.cate_name||"无" }}';
            return '<div>' + '<span data-id="{{ d.' + key + ' }}" class="select-category layui-btn layui-btn-sm jl-control-' + key + '">' + str + '</span>' + '</div>';
        },
        img: function img(key, d) {
            return '<div>' + '<div class="layui-btn-group"> ' + '<span class="layui-btn layui-btn-sm jl-control-' + key + '" data-img=" ' + d[key] + ' " style="background-color:' + (d[key] ? "#009688" : "#c2c2c2") + '">查看</span>' + '<span class="layui-btn layui-btn-sm action-upload-img action-upload-img-self">上传</span>' + '</div>' + '</div>';
        },
        pdf: function pdf(key, d) {
            return '<div>' + '<div class="layui-btn-group"> ' + '<button class="layui-btn layui-btn-sm jl-control-' + key + '" data-pdf=" ' + d[key] + ' " style="background-color:' + (d[key] ? "#009688" : "#c2c2c2") + '">查看</button>' + '<button class="layui-btn layui-btn-sm action-upload-pdf action-upload-pdf-self">上传（仅限pdf）</button>' + '</div>' + '</div>';
        },
        priceInfo: function priceInfo(key) {
            return '<div>' + '<span class="price-data">{{JSON.stringify(d.' + key + ')}} </span>' + '<button class="price-tpl layui-btn layui-btn-sm jl-control-' + key + '" >查看详情</button>' + '</div>';
        },
        describe: function describe(key) {
            return '<div>' + '<button data-describe="{{ d.' + key + ' }}" class="describe-tpl layui-btn layui-btn-sm jl-control-' + key + '">编辑</button>' + '</div>';
        },
        selectErpProduct: function selectErpProduct(key, option) {
            var strs = '';
            var key_id = key + "_id";
            //console.log(option);
            if (option) {
                strs = '<input type="text" value="' + (option[key] ? option[key] : option[key_id] ? option[key_id] : "") + '" name="title" autocomplete="off" class="jl-select-erp-product layui-input jl-control-' + key + '">' + '<input type="hidden" value="' + option[key_id] + '" name="p_id" autocomplete="off" class="jl-select-erp-product layui-input jl-control-' + key + '-id">';
            } else {
                strs = '<input type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="jl-select-erp-product layui-input jl-control-' + key + '">';
            }

            return '<div>' + strs + '</div>';
        },
        selectPm: function selectPm(key) {
            return '<div>' + '<input type="text" value="{{ d.' + key + ' }}" name="title" autocomplete="off" class="jl-select-pm layui-input jl-control-' + key + '">' + '</div>';
        },
        input_area: function input_area(key, d) {
            var keyStart = key + "_start";
            var keyEnd = key + "_end";

            return ' <div class="layui-form-item">\n' + '    <div class="layui-inline">\n' + '      <label class="layui-form-label">' + d.name + '<input type="hidden" name="' + key + '_name" value="' + d.name + '"/><input type="hidden" name="' + key + '_type" value="' + d[key + "_type"] + '"/></label>\n' + '      <div class="layui-input-inline" style="width: 120px;">' + '      <input type="text" value="' + (d[keyStart] || d[key + "_length"] || "") + '" placeholder="最小值" name="' + (key === "volume" ? "volume_length" : keyStart) + '"   min="0" max="99999999"  autocomplete="off" class="jl-limit-min layui-input jl-control-' + key + '_start">' + '      </div>\n' + '      <div class="layui-form-mid">-</div>\n' + '      <div class="layui-input-inline" style="width: 120px;">\n' + '        <input type="text"  value="' + (d[keyEnd] || d[key + "_width"] || "") + '" name="' + (key === "volume" ? "volume_width" : keyEnd) + '" placeholder="最大值"  autocomplete="off" min="0" max="99999999" class="layui-input jl-control-' + key + '_end">' + '      </div>\n' + '    </div>\n' + '  </div>';
        },
        selectAttr: function selectAttr(check_value, option) {
            var optionHtml = '';
            if ((typeof check_value === 'undefined' ? 'undefined' : _typeof(check_value)) === 'object') {
                var option_array = check_value[check_value.key + "_value"].split(";");
                $.each(option_array, function (index, value) {
                    if (check_value[check_value.key + "_start"] == value || $.inArray(check_value.key, ['volume']) > -1 && check_value[check_value.key + "_length"] == value) {
                        optionHtml += '<option value="' + value + '" selected>' + value + '</option>';
                    } else {
                        optionHtml += '<option value="' + value + '" >' + value + '</option>';
                    }
                });
                return '<div class="layui-form-item">' + ' <label class="layui-form-label">' + check_value.name + '<input type="hidden" name="' + check_value.key + '_name" value="' + check_value.name + '"/><input type="hidden" name="' + check_value.key + '_type" value="' + check_value[check_value.key + "_type"] + '"/></label>' + '<div class="layui-input-block" style="width: 58%;">' + '<select style="100px" class="jl-control-' + check_value.key + '" name="' + check_value.key + '_' + ($.inArray(check_value.key, ['volume']) > -1 ? "length" : "start") + '" lay-filter="">' + optionHtml + '</select>' + '</div>' + '</div>';
            } else {
                $.each(option, function (index, value) {
                    if (check_value == value.id) {
                        optionHtml += '<option value="' + value.id + '" selected>' + value.attr_name + '</option>';
                    } else {
                        optionHtml += '<option value="' + value.id + '" >' + value.attr_name + '</option>';
                    }
                });
                return '<div class="layui-form-item">' + ' <label class="layui-form-label">属性类别</label>' + '<div class="layui-input-block" style="width: 58%;">' + '<select style="100px" class="jl-control-' + check_value + '" name="attr_id" lay-filter="attrSelect">' + optionHtml + '</select>' + '</div>' + '</div>';
            }
        },
        input_input: function input_input(obj) {
            //console.log(obj);
            var values = obj.value ? obj.value : obj[obj.key + "_length"] ? obj[obj.key + "_length"] : obj[obj.key + "_start"] ? obj[obj.key + "_start"] : obj[obj.key];
            return ' <div class="layui-form-item">' + '    <label class="layui-form-label">' + obj.name + '<input type="hidden" name="' + obj.key + '_name" value="' + obj.name + '"/><input type="hidden" name="' + obj.key + '_type" value="' + obj[obj.key + "_type"] + '"/></label>' + '    <div class="layui-input-block" style="width: 262px;">' + '      <input type="text" name="' + (obj[obj.key + "_type"] ? obj.key === "volume" ? obj.key + "_length" : obj.key + "_start" : obj.key) + '" lay-verify="' + obj.key + '" autocomplete="off" placeholder="请输入' + obj.name + '" class="layui-input layui-input-one-input" value="' + (values || "") + '"> ' + '    </div>' + ' </div>';
        },
        input_radio: function input_radio(obj, isValue) {
            var str = '';
            var show_value = false;
            $.each(obj.arr, function (ind, val) {
                if (val.isChecked) {
                    if (val.value == 1) {
                        show_value = true;
                    }
                    str += '<input type="radio" name="' + val.key + '" lay-filter="input_check" value="' + val.value + '" title="' + val.name + '" checked/>';
                } else {
                    str += '<input type="radio" name="' + val.key + '" lay-filter="input_check" value="' + val.value + '" title="' + val.name + '"/>';
                }
            });
            var input_str = '';
            if (isValue) {
                input_str = ' <div class="layui-form-item value-input-change" style="display:' + (show_value ? "block" : "none") + '">\n' + '    <label class="layui-form-label">' + obj.name.split("类型")[0] + '预定值</label>\n' + '    <div class="layui-input-block">\n' + '      <input type="text" name="' + obj.key.split('_type')[0] + '_value"  placeholder="多个不通值请用分号(;)隔开" autocomplete="off" class="layui-input" value="' + (obj.value || "") + '">\n' + '    </div>\n' + '  </div>';
            };
            return '<div class="layui-form-item" >\n' + '    <label class="layui-form-label">' + obj.name + '</label>\n' + '    <div class="layui-input-block">\n' + str + '    </div>' + '</div>' + input_str;
        }
    });
});